<template>
  <div class="min-h-screen">
    <div class="container mx-auto px-4 py-8">
      <!-- Header -->
      <div class="text-center mb-8">
        <h1 class="text-3xl font-cal text-zoom-700 dark:text-zoom-300 mb-2">Settings</h1>
        <p class="text-gray-600 dark:text-gray-300 font-poppins">Customize your Zoomka experience</p>
      </div>

      <!-- Settings Cards -->
      <div class="max-w-md mx-auto space-y-4">
        <!-- Appearance -->
        <div class="glass-card p-6">
          <h3 class="text-lg font-semibold text-gray-800 dark:text-white mb-4 flex items-center space-x-2">
            <Icon icon="material-symbols:palette" class="text-zoom-500" />
            <span>Appearance</span>
          </h3>
          <div class="space-y-3">
            <button 
              @click="toggleDarkMode"
              class="w-full flex items-center justify-between p-3 rounded-xl transition-all duration-300"
              :class="isDarkMode ? 'bg-gray-200 dark:bg-gray-700 text-gray-800 dark:text-white' : 'bg-zoom-500 text-white'"
            >
              <span class="flex items-center space-x-2">
                <Icon :icon="isDarkMode ? 'material-symbols:dark-mode' : 'material-symbols:light-mode'" />
                <span>{{ isDarkMode ? 'Dark Mode' : 'Light Mode' }}</span>
              </span>
              <Icon icon="material-symbols:check" v-if="!isDarkMode" />
            </button>
          </div>
        </div>

        <!-- Language -->
        <div class="glass-card p-6">
          <h3 class="text-lg font-semibold text-gray-800 dark:text-white mb-4 flex items-center space-x-2">
            <Icon icon="material-symbols:translate" class="text-zoom-500" />
            <span>Language</span>
          </h3>
          <select class="input-modern">
            <option value="en">English</option>
            <option value="sw">Swahili</option>
            <option value="ar">Arabic</option>
            <option value="zh">Chinese</option>
            <option value="ru">Russian</option>
          </select>
        </div>

        <!-- Storage -->
        <div class="glass-card p-6">
          <h3 class="text-lg font-semibold text-gray-800 dark:text-white mb-4 flex items-center space-x-2">
            <Icon icon="material-symbols:database" class="text-zoom-500" />
            <span>Storage</span>
          </h3>
          <div class="space-y-2">
            <div class="flex justify-between text-sm">
              <span class="text-gray-600 dark:text-gray-300">Contacts</span>
              <span class="text-zoom-500">3</span>
            </div>
            <div class="flex justify-between text-sm">
              <span class="text-gray-600 dark:text-gray-300">Business Profiles</span>
              <span class="text-zoom-500">1</span>
            </div>
            <div class="flex justify-between text-sm">
              <span class="text-gray-600 dark:text-gray-300">App Size</span>
              <span class="text-zoom-500">2.4 MB</span>
            </div>
          </div>
        </div>

        <!-- About -->
        <div class="glass-card p-6 text-center">
          <Icon icon="material-symbols:rocket-launch" class="text-4xl text-zoom-500 mb-4" />
          <h3 class="text-lg font-semibold text-gray-800 dark:text-white mb-2 font-cal">Zoomka</h3>
          <p class="text-sm text-gray-600 dark:text-gray-300 font-poppins">Version 1.0.0</p>
          <p class="text-xs text-gray-500 dark:text-gray-400 mt-2 font-poppins">
            Zoom your business across Africa
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { Icon } from '@iconify/vue'
import { ref, onMounted } from 'vue'

const isDarkMode = ref(false)

onMounted(() => {
  const saved = localStorage.getItem('zoomka-dark-mode')
  isDarkMode.value = saved ? JSON.parse(saved) : false
})

const toggleDarkMode = () => {
  isDarkMode.value = !isDarkMode.value
  localStorage.setItem('zoomka-dark-mode', JSON.stringify(isDarkMode.value))
  document.documentElement.classList.toggle('dark', isDarkMode.value)
}
</script>